.ColorBox{
    width: 20%;
    height: 25%;
    margin: 0 auto;
    display: inline-block;
    position: relative;
    cursor: pointer;
    margin-bottom: -3.9px;
    
} 

.SingleColorPalette .ColorBox{
    height: 50%;
}

.ColorBox.go-back{
    background-color: black;
}

.copy-button, 
.back-button {
    /* copybutton class AND backbutton class */
    width: 100px;
    height: 30px;
    position: absolute;
    display: inline-block;
    top: 50%;
    left: 50%;
    margin-left: -50px;
    margin-top: -15px;
    text-align: center;
    outline: none;
    background: rgba(255, 255, 255, 0.3);
    font-size: 1rem;
    line-height: 30px;
    color: white;
    text-transform: uppercase;
    border: none;
    border-radius: 3px;
    text-decoration: none;
    
}
